@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
*{margin:0px;padding:0px;font-family:"Open Sans",sans-serif;box-sizing:border-box}.app{background-color:#e4e5f1;overflow-x:hidden;width:100vw;height:100vh}.app.sun{background-color:#161722}.container{display:flex;flex-direction:column;align-items:center;height:18.5em;width:100vw;object-fit:cover;background-repeat:no-repeat;background-position:center;display:flex;animation:intro .75s linear both}.container.sun{background-image:url(/todo-app-main/static/media/bg-desktop-dark.73e47dbb.jpg)}.container.moon{background-image:url(/todo-app-main/static/media/bg-desktop-light.3508d620.jpg)}.box{min-width:350px;width:400px;display:flex;justify-content:space-between}.box.sun{background-color:#393a4c}.box.sun>form>input{background-color:#393a4c}.box.sun>.ul-todos>.li-todo>.container-checkbox>.checkmark{background-color:#4d5066}.box-header{margin:5em 0 3em 0;align-items:center}.title{color:#fafafa;letter-spacing:.3em}.box-content{border-radius:.25em;background-color:#fafafa;margin:1em 0 1em 0;display:flex;flex-direction:column;justify-content:space-between;box-shadow:0 15px 15px rgba(37,39,60,.5)}.footer-box{display:flex;justify-content:space-between;padding:.5em 1.5em}.footer-box>span{display:flex;align-items:center}.action{cursor:pointer;background-color:transparent;background-repeat:no-repeat;border:none;object-fit:cover;margin:.25em;font-size:.8em}.form{display:flex;align-items:center;width:100%;height:3em;contain:content;padding:.5em 1.5em}.form-control{width:100%;height:3em;border:none;border-radius:.25em;padding-left:1.25em;font-weight:bold}.form-control:focus{outline:none}.btn{cursor:pointer;width:2.4em;height:2.4em;background-color:transparent;background-repeat:no-repeat;border:none;object-fit:cover}.btn.btn-moon{transition:all .5 ease-in-out;background-image:url(/todo-app-main/static/media/icon-moon.6c03114b.svg)}.btn.btn-sun{transition:all .5 ease-in-out;background-image:url(/todo-app-main/static/media/icon-sun.910b1f9a.svg)}.ul-todos{list-style:none;width:100%}.li-todo{display:flex;align-items:center;height:3em;padding:.5em 1.5em}.li-todo:hover{transition:color .8s linear .9s}.li-todo:hover .btn-close{box-sizing:content-box}.li-todo:hover .btn-close::before{content:"";position:absolute;width:2em;height:.15em;background-color:#777a92;border-radius:.1em;transform:translateX(-1em) rotate(45deg)}.li-todo:hover .btn-close::after{content:"";position:absolute;width:2em;height:.15em;background-color:#777a92;border-radius:.1em;transform:translateX(-1em) rotate(-45deg)}.li-todo>.description{width:100%;height:100%;display:flex;align-items:center;padding-left:1.25em;cursor:pointer;font-weight:bold}.li-todo>.description.complete{text-decoration:line-through;color:#777a92}.separator{background-color:#777a92;height:1px;opacity:.5;width:100%}span,.action{color:#777a92;font-size:.8em;font-weight:600}.container-checkbox{padding-left:1.75em;transform:translateY(-0.7em);cursor:pointer}.container-checkbox input{position:absolute;opacity:0;cursor:pointer}.checkmark{position:absolute;top:0;left:0;height:25px;width:25px;background-color:#eee;border-radius:100%}.container-checkbox:hover input~.checkmark{background-color:#ccc;border-top:1px solid #57ddff;border-left:1px solid #57ddff;border-right:1px solid #c058f3;border-bottom:1px solid #c058f3}.container-checkbox:hover input:checked~.checkmark{background-color:#ccc;border:none}.container-checkbox input:checked~.checkmark{background:linear-gradient(-225deg, #57ddff 0%, #c058f3 100%)}.checkmark:after{content:"";position:absolute;display:none}.container input:checked~.checkmark:after{display:block;transition:all .5s ease-in-out}.container .checkmark:after{left:9px;top:5px;width:5px;height:10px;border:solid #fff;border-width:0 3px 3px 0;transform:rotate(45deg)}.mobile{display:none;align-items:center}.active{color:#3a7bfd}@keyframes intro{from{transform:translateY(-100em)}to{transform:translateY(0)}}@media screen and (max-width: 450px){.box{width:300px}.desk{display:none}.mobile{display:flex}}
